*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap');
:root{
    
    --font:  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.main-container{
    width: 100%;
    height: auto;
    background-color: rgb(12, 0, 56);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}
.wrapper{
    width: 70%;
    height: auto;
  
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.form{

    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3em;
    padding-bottom: 3em;

}
.socials-holder{

    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1em;
    
}
.socials-holder>input{
    width: 100%;
    color: white;
    padding: 0.3em 0.5em;
    border: 1px rgba(255, 255, 255, 0.471) solid;
    border-radius: 0.3em;
    background-color: rgba(255, 255, 255, 0.226);
    font-size: 1.2em;
}
.socials-holder>textarea{
    width: 100%;
    color: white;
    padding: 0.3em 0.5em;
    border: 1px rgba(255, 255, 255, 0.471) solid;
    border-radius: 0.3em;
    background-color: rgba(255, 255, 255, 0.226);
    font-size: 1.2em;
}
.languages-holder{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1em;
}
.languages{
    display: flex;
    flex-wrap: wrap;
    gap: 0.6em;
    justify-content: center;
    align-items: center;
}
.languages>div{
    padding: 0.6em 1em;
    background-color: rgba(255, 255, 255, 0.231);
    border-radius: 4em;
    border: 1px rgba(255, 255, 255, 0.27) solid;
}
.languages>div:hover{
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.415);
    
}
.selected{
    background-color: rgba(168, 255, 80, 0.545)!important;
    border: 1px rgb(168, 255, 19) solid;
}
.h1Style{
    font-size: 2em;
    font-weight: 500;
    
}
.submit-btn-div{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.submit-btn{
    width: 100%;
    padding: 0.3em 0.5em;
    border: 1px rgba(255, 255, 255, 0.471) solid;
    border-radius: 0.3em;
    background-color: rgba(255, 255, 255, 0.226);
    font-size: 1.2em;
    color: white;

}
.submit-btn:hover{
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.557);
}
.gif-input-div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;

}
.gif-input-div>input{
    display: none;
}
.custom-file-input {
    display: inline-block;
    padding: 8px 12px;
    background-color: #e9e9e9;
    color: #333;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }

  /* Style the file name display */
  .file-name {
    margin-top: 8px;
    color: #777;
   
    
  }